import React from "react";
import classes from "./Repos.module.css";

const repos = (props) => {
  let el;
  if (props.repos !== null) {
    el = props.repos.data.map((el) => {
      return (
        <a href={el.svn_url} className={classes.Repos}>
          <h3 key={props.id}>{el.name}</h3>
          <p>
            {el.description === null
              ? "No Desription provided"
              : el.description}
          </p>
        </a>
      );
    });
  } else {
    el = <p></p>;
  }
  return el;
};
export default repos;
