import React from "react";
import classes from "./Profile.module.css";

const profile = (props) => {
  let el;
  if (props.user !== null) {
    el = (
      <div className={classes.Profile}>
        <h1>{props.user.data.login}</h1>
        <img src={props.user.data.avatar_url} alt="" />
      </div>
    );
  } else {
    el = <p>No User Found</p>;
  }
  return el;
};
export default profile;
