import React, { Component } from "react";
import Input from "../../components/Input/Input";
import classes from "./Layout.module.css";
import axios from "axios";
import Profile from "../../components/Profile/Profile";
import Repos from "../../components/Repos/Repos";

class Layout extends Component {
  state = {
    searchBool: false,
    profile: null,
    userName: "MihirSharmaC",
    repos: null,
    client_id: "6c0d7a13ccc3858c3e7f",
    client_secret: "eb21c853c72d4bd513b251a723fe778a356cf70a",
  };

  userInputHandler = (event) => {
    this.setState({
      userName: event.target.value,
    });
  };

  searchHandler = () => {
    this.setState({ searchBool: true });
  };

  componentDidUpdate() {
    if (this.state.searchBool) {
      axios
        .get(
          `https://api.github.com/users/${this.state.userName}?client_id=${this.state.client_id}&client_secret=${this.state.client_secret}`
        )
        .then((res) => {
          this.setState({
            profile: { ...res },
          });
          axios
            .get(
              `https://api.github.com/users/${this.state.userName}/repos?per_page=5&sort=created&client_id=${this.state.client_id}&client_secret=${this.state.client_secret}`
            )
            .then((res) => {
              console.log(res);
              this.setState({
                repos: res,
              });
            });
          this.setState({ searchBool: false });
        });
    }
  }

  render() {
    return (
      <div className={classes.Layout}>
        <Input
          search={this.searchHandler}
          inputHandler={this.userInputHandler}
        />
        <Profile user={this.state.profile} />
        <Repos repos={this.state.repos} />
      </div>
    );
  }
}

export default Layout;
